.page-tags .tag-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.page-tags .tag-item{
  margin: 10px;
  padding: 0;
  width: calc(100% * .5 - 20px);
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
}

.page-tags .tag-item > a {
  height: 300px;
  display: block;
  position: relative;
}

.page-tags .tag-item > a h2 {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin-bottom: 0;
  padding: 16px;
  font-size: 19px;
  font-weight: 400;
  color: #fff;
  background: rgba(0,0,0,0.4);
}
.post-hero__tags {
  margin-bottom: 20px;
}